  height: 0;
  display: block;
  border-style: solid;
  border-width: 10px 6px 0 6px;
  border-color: #000000 transparent transparent transparent;
}

.c-icon.-arrow2::before {
  position: absolute;
  content: '';
  display: block;
  width: 10px;
  height: 10px;
  border: 2px #000 solid;
  border-left: 0;
  border-top: 0;
}

.c-icon.-arrow2::before {
  transform: rotate(45deg);
}

.c-icon.-arrow2::after {
  transform: rotate(-45deg);
