  position: absolute;
  top: 50%;
  z-index: 10;
  width: 100%;
  height: 200px;
  margin: -100px 0 0;
  overflow: visible;
}

@media (min-width: 980px) {
  .p-kv__inner {
    height: 420px;
    margin: -210px 0 0;
  }
}

.p-kv__title {
  position: absolute;
  top: 50%;
  left: 50%;
  left: 50%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px 0 0;
  overflow: hidden;
  line-height: 1.1;
  color: #fff;
  text-align: center;
  transform: translate(-50%, -50%);
}

.p-kv__title .jp {
  font-size: 24px;
  font-size: 1.5rem;
}

