  position: relative;
  top: 0;
  left: 0;
  z-index: 0;
  width: 100%;
  height: inherit;
  margin: 0;
}

@media (min-width: 980px) {
  .p-kv__bg {
    min-width: 900px;
    min-width: inherit;
    height: 420px;
  }
}

.p-kv__bg img {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  min-width: 900px;
  height: auto;
  transform: translate(-50%, -50%);
