.l-sub {
  width: 280px;
  padding: 20px;
  margin-left: 20px;
  background: #efefef;
  &__section {}
  &__title {
    @include fz(16);
  }
  &__list {
    margin-top: 5px;
    margin-left: 10px;
  }
  &__item {
    a {
      text-decoration: none;
      &:hover {
        text-decoration: underline;
      }
    }
  }
  &__section {
    & + & {
      margin-top: 30px;
    }
  }
}