//リセットCSSを追加
//
//Super Form Reset.css
//https://gist.github.com/anthonyshort/552543

.p-form {
  //@extend .ex-input; 
  .ex-input {
    background-color: #fff;
    padding: 6px 10px;
    @include fz(14);
    width: 100%;
    border: none;
  }

  h3 {
    margin: 0 0 12px;
  } //☆リード文 START
  &__lead {
    @include fz(18);
    &Text {
      margin: 0 0 40px;
    }
  } //☆リード文 END
  //☆先頭エラー文 START
  &__errorWrap {
    margin: 0 0 40px;
    &__title {
      margin: 0 0 12px;
      @include fz(16);
      font-weight: bold;
      color: #EF5A24;
    }
    &__list {
      padding: 20px 20px 12px;
      margin: 0;
      list-style: none;
      border: 2px solid #EF5A24;
      border-radius: 4px;
    }
    &__item {
      margin: 0 0 12px;
      @include fz(16);
      color: #EF5A24;
    }
  } //☆先頭エラー文 END
  &__group {
    margin: 0 0 32px;
  }
  &__label {}
  &__must {}
  &__must {
    display: inline-block;
    height: 18px;
    padding: 0 10px;
    margin: 0 0 0 8px;
    @include fz(12);
    line-height: 18px;
    color: #fff;
    vertical-align: middle;
    background: #999;
    border-radius: 9px;
  }
  &__column {
    display: table;
    width: 100%;
    &Title {
      display: table-cell;
      width: 200px;
      padding: 0 18px 0 0;
      vertical-align: top;
    }
    &Body {
      display: table-cell;
      vertical-align: top;
    }
    &Error {
      display: block;
      margin: 8px 0 0;
      @include fz(11);
      color: #EF5A24;
    }
  }
  &__privacy {
    height: 100px;
    padding: 1em;
    margin: 0 0 40px;
    overflow: hidden;
    overflow: auto;
    background: #fff;
    border: 1px solid #333;
    &Inner {
      @include fz(12);
      line-height: (24/12);
    }
  }
  &__btnWrap {
    margin: 0 0 40px;
    text-align: center;
  }
}